console.log('Starting script');         //info w konsoli o starcie skryptu
import Notiflix from 'notiflix';                       // import biblioteki Notiflixa (obsuga komunikatw i stylw css)
import 'notiflix/dist/notiflix-3.2.6.min.css';

import axios from 'axios';                                // import axiosa - biblioteki do odbsugi da http
axios.defaults.headers.common['x-api-key'] ='live_iPaBzyy1iOjvPQNHzNKNExHWu1HC402dvsZ3klFHj2GCZBHTFhAkZ1tIADh4TvP';

function fetchBreeds(callback) {                                   // deklaracja funkcji do obsugi danych z API
    return axios
    .get('https://api.thecatapi.com/v1/breeds')              // wysanie dania aby pobra dane z API o rasach kotw
    .then(response => {                                    // jak danie bdzie z sukcesem to odpowied ma by przetworzona
        return response.data;                                      // zawracane dane o rasach kotw
    })

    .catch(error => {                                       // obsuga bdu
        console.log('fetchBreeds error', error.response);         // wywietlenie bdu w konsoli
        return Promise.reject(error);                          // odrzucenie promisa z bedem
    });
}

function fetchCatByBreed(breedId, callback) {                               // delaracja funkcji do cigania obrazkw kotw
    return axios
    .get(`https://api.thecatapi.com/v1/images/search?breed_ids=${breedId}`)        // wysanie dania get  aby pobra z API danego kota
    .then(response => response.data[0])                                        // przetwarzanie tego dania  - pobranie pierwszego obrazu
    .catch(error => {
            console.log('fetchCatByBreed error', error.response);           // obsuga bdu , podobnie
            return Promise.reject(error);
    });
}
export { fetchBreeds, fetchCatByBreed };                                //eksport funkcji do uycia w pliku index.js
