
import SlimSelect from 'slim-select';                            //import narzdzi
import 'slim-select/dist/slimselect.css';
import Notiflix from 'notiflix';
import 'notiflix/dist/notiflix-3.2.6.min.css';

import { fetchBreeds, fetchCatByBreed } from './cat-api';

const catInfo = document.querySelector('.cat-info');         // wskazanie miejsca na stronie gdzie bd wywietlane informacje o kotach
let select;                                                             // zmienna do obsugi listy

Notiflix.Loading.standard('Loading...', {       // wywietlenie komunikatu "loadnig"
    backgroundColor: 'rgba(0,0,0,0.8)',
});
window.addEventListener('DOMContentLoaded', () => {                                // adowanie caej strony
    Notiflix.Loading.standard('Loading data, please wait...', {          // komunikat o adowaniu strony
        backgroundColor: 'rgba(0,0,0,0.8)',
    });

    select = new SlimSelect({                                               // utworzenie listy rozwijanej
        select: '.breed-select',
    });

    fetchBreeds()                                                                      // pobranie danych o rasach
        .then(breeds => {
            Notiflix.Loading.remove();                                                 // usunicie komunikatu "loading"
            select.setData(                                                           // wypenienie listy rozwijanej danymi o rasach
                breeds.map(breed => ({
                    text: breed.name,
                    value: breed.id,
                }))
            );
        })
        .catch(error => {
            Notiflix.Loading.remove();                                                                // usunicie napisu "loading" i wywietlenie komunikatu o bdzie
            Notiflix.Notify.failure('Oops! Something went wrong! Try reloading the page!');
        });


    select.onChange = () => {                                                                   // nasuch na zmian w licie rozwijanej
        const selectedBreedId = select.selected();                                 // pobranie wybranej rasy kota
        if (selectedBreedId) {       
            fetchCatByBreed(selectedBreedId)                                  // majd ID pobieramy info o danej rasie
                .then(catData => {
                    updateCatInfo(catData);                                           // wywietlenie info o kocie na stronie
                })
                .catch(error => {
                
                    Notiflix.Notify.failure(                                           // wywietlenie komunikatu o bdzie
                        'Oops! Something went wrong! Try reloading the page!');
                });
        }
    };
});
    

    function updateCatInfo(catData) {                                          // funkcja aktualizujca info o kocie na stronie
        const { url, breed } = catData;
        catInfo.innerHTML = `
    <img src="${url}" alt="${breed.name}" />
    <div class="description">
    <h2>${breed.name}</h2>
    <p>${breed.description}</p>
    <p>${breed.temperament}</p>
    </div>
    `;
    }



